import React from "react";
// nodejs library that concatenates classes
import classNames from "classnames";
// nodejs library to set properties for components
import PropTypes from "prop-types";
// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
// import Button from "@material-ui/core/Button";
import Hidden from "@material-ui/core/Hidden";
import Drawer from "@material-ui/core/Drawer";
// @material-ui/icons
import Menu from "@material-ui/icons/Menu";
// core components
import headerStyle from "assets/jss/material-kit-react/components/headerStyle.jsx";

class Header extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      mobileOpen: false
    };
    this.handleDrawerToggle = this.handleDrawerToggle.bind(this);
    this.headerColorChange = this.headerColorChange.bind(this);
  }
  handleDrawerToggle() {
    this.setState({ mobileOpen: !this.state.mobileOpen });
  }
  componentDidMount() {
    if (this.props.changeColorOnScroll) {
      window.addEventListener("scroll", this.headerColorChange);
    }
  }
  headerColorChange() {
    const { classes, color, changeColorOnScroll } = this.props;
    const windowsScrollTop = window.pageYOffset;
    if (windowsScrollTop > changeColorOnScroll.height) {
      document.body
        .getElementsByTagName("header")[0]
        .classList.remove(classes[color]);
      document.body
        .getElementsByTagName("header")[0]
        .classList.add(classes[changeColorOnScroll.color]);
    } else {
      document.body
        .getElementsByTagName("header")[0]
        .classList.add(classes[color]);
      document.body
        .getElementsByTagName("header")[0]
        .classList.remove(classes[changeColorOnScroll.color]);
    }
  }
  componentWillUnmount() {
    if (this.props.changeColorOnScroll) {
      window.removeEventListener("scroll", this.headerColorChange);
    }
  }
  render() {
    const {
      classes,
      color,
      rightLinks,
      leftLinks,
      fixed,
      absolute
    } = this.props;
    const appBarClasses = classNames({
      [classes.appBar]: true,
      [classes[color]]: color,
      [classes.absolute]: absolute,
      [classes.fixed]: fixed
    });
    const brandComponent = (
      <a href="/">
      <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1457.38452 1298.23135" width="40" height="40"><title>Asset 2</title><circle cx="728.88452" cy="649.78038" r="432" fill="#1360EA"/><path d="M898.69531,354.64842A1480.54258,1480.54258,0,0,0,794.25684,224.713c115.07568-101.37085,226.40479-144.40076,299.10278-102.42859,57.48633,33.18976,81.01587,113.879,73.39038,220.52954,26.94946-157.58447,4.20117-279.78149-73.71167-324.76453-87.6322-50.59454-224.96436,8.09131-364.34607,139.55817C589.31049,26.14073,451.97833-32.54512,364.34613,18.04942c-74.14374,42.80688-98.32635,155.54156-77.2688,302.15118-3.78784-97.60455,20.23077-171.0545,74.39984-202.329,73.462-42.41333,186.3717,1.97479,302.7326,105.64746A1478.85829,1478.85829,0,0,0,558.68921,354.64842C238.24518,386.467,0,506.22917,0,649.1154c0,85.61371,85.53955,162.92377,223.03564,217.99213C136.614,821.58568,85.0141,764.06,85.0141,701.51109c0-113.34613,169.42529-210.20074,408.50739-249.00555q-13.72192,22.21692-27.02368,45.23181c-201.22278,348.52814-246.95764,698.84027-102.15167,782.444,74.14374,42.807,183.866,7.38245,300.30487-84.15881-82.634,52.08264-158.25275,68.00684-212.42175,36.73242-127.77-73.76807-87.82611-382.15625,89.21716-688.8042,21.24371-36.79523,43.49823-71.8866,66.43219-105.03662,38.3324-2.97858,77.86066-4.54071,118.26593-4.54071,42.48425,0,83.99951,1.72632,124.17334,5.01154,21.7478,31.70886,42.86816,65.15808,63.07251,100.15308,177.04334,306.64795,216.98718,615.036,89.21729,688.80408-57.48645,33.1897-139.13013,13.22217-227.67932-46.70691,122.99744,102.13123,240.19714,143.52917,318.11,98.54614,144.806-83.60376,99.07117-433.91589-102.15161-782.444q-13.00928-22.53287-26.448-44.29779c236.08545,39.40759,402.8363,135.62372,402.8363,248.07153,0,66.37952-58.11414,127.1012-154.2887,173.82251,149.94678-55.45337,244.39819-136.2522,244.39819-226.2182C1457.38452,506.22917,1219.1394,386.467,898.69531,354.64842Zm-170.00305-8.28906q-26.01151,0-51.57446.75269,25.96536-31.52271,52.36932-59.52972,26.10965,28.00552,51.87811,59.56158Q755.26978,346.36937,728.69226,346.35936Z" fill="#61dafb"/></svg>      
      </a>
    );
    return (
      <AppBar className={appBarClasses}>
        <Toolbar className={classes.container}>
          {leftLinks !== undefined ? brandComponent : null}
          <div className={classes.flex}>
            {leftLinks !== undefined ? (
              <Hidden smDown implementation="css">
                {leftLinks}
              </Hidden>
            ) : (
              brandComponent
            )}
          </div>
          <Hidden smDown implementation="css">
            {rightLinks}
          </Hidden>
          <Hidden mdUp>
            <IconButton
              color="inherit"
              aria-label="open drawer"
              onClick={this.handleDrawerToggle}
            >
              <Menu />
            </IconButton>
          </Hidden>
        </Toolbar>
        <Hidden mdUp implementation="css">
          <Drawer
            variant="temporary"
            anchor={"right"}
            open={this.state.mobileOpen}
            classes={{
              paper: classes.drawerPaper
            }}
            onClose={this.handleDrawerToggle}
          >
            <div className={classes.appResponsive}>
              {leftLinks}
              {rightLinks}
            </div>
          </Drawer>
        </Hidden>
      </AppBar>
    );
  }
}

Header.defaultProp = {
  color: "white"
};

Header.propTypes = {
  classes: PropTypes.object.isRequired,
  color: PropTypes.oneOf([
    "primary",
    "info",
    "success",
    "warning",
    "danger",
    "transparent",
    "white",
    "rose",
    "dark"
  ]),
  rightLinks: PropTypes.node,
  leftLinks: PropTypes.node,
  brand: PropTypes.string,
  fixed: PropTypes.bool,
  absolute: PropTypes.bool,
  // this will cause the sidebar to change the color from
  // this.props.color (see above) to changeColorOnScroll.color
  // when the window.pageYOffset is heigher or equal to
  // changeColorOnScroll.height and then when it is smaller than
  // changeColorOnScroll.height change it back to
  // this.props.color (see above)
  changeColorOnScroll: PropTypes.shape({
    height: PropTypes.number.isRequired,
    color: PropTypes.oneOf([
      "primary",
      "info",
      "success",
      "warning",
      "danger",
      "transparent",
      "white",
      "rose",
      "dark"
    ]).isRequired
  })
};

export default withStyles(headerStyle)(Header);



